<template><section><html><head></head><body><h1>PolygonGeometry、PolygonOutlineGeometry</h1>
<ul>
<li><code v-pre="">vc-geometry-polygon</code> 组件用于加载多边形几何体。</li>
<li><code v-pre="">vc-geometry-outline-polygon</code> 组件用于加载多边形几何体轮廓。</li>
<li>需要作为 <code v-pre="">vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre="">vc-primitive</code> 或 <code v-pre="">vc-primitive-ground</code>。</li>
</ul>
<h2>示例</h2>
<h3>加载多边形几何体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry>
            <vc-geometry-polygon ref="polygon" :vertex-format="vertexFormat" :polygon-hierarchy="polygonHierarchy" :height="height" :extruded-height="30"></vc-geometry-polygon>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearanceOutline">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-outline-polygon ref="polygonOutline" :polygon-hierarchy="polygonHierarchyOutline">
            </vc-geometry-outline-polygon>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
