<h2>属性</h2>
<h3><code v-pre="">vc-geometry-polygon</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>polygonHierarchy</td>
<td>Object|Array</td>
<td></td>
<td><code v-pre="">optional</code> 指定 polygon 的 PolygonHierarchy 属性。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> 指定 polygon 的高度。</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定 polygon 拉伸高度。</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 polygon 要缓存的顶点属性。</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number|Object</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code> 指定 polygon 纹理按正北方向逆时针旋转角度。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定 polygon 参考椭球体。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 指定每个经纬度之间的采样粒度。</td>
</tr>
<tr>
<td>perPositionHeight</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> 指定 polygon 是否使用每个位置的高度。</td>
</tr>
<tr>
<td>closeTop</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定 polygon 拉伸出来的顶部是否闭合。</td>
</tr>
<tr>
<td>closeBottom</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定 polygon 拉伸出来的底部是否闭合。</td>
</tr>
<tr>
<td>arcType</td>
<td>Number</td>
<td><code v-pre="">1</code></td>
<td><code v-pre="">optional</code> 指定 polygon 线条类型。<strong>NONE: 0, GEODESIC: 1, RHUMB: 2</strong></td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre="">vc-geometry-outline-polygon</code></h3>
<table>
<thead>
<tr>
