<ul>
<li>polygonHierarchy 结构</li>
</ul>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-comment">// Array</span>
[{<span class="hljs-attr">lng</span>: number, <span class="hljs-attr">lat</span>: number, <span class="hljs-attr">height</span>: number},...,{<span class="hljs-attr">lng</span>: number, <span class="hljs-attr">lat</span>: number, <span class="hljs-attr">height</span>: number}]
<span class="hljs-comment">// Object</span>
{
  <span class="hljs-attr">positions</span>: [{<span class="hljs-attr">lng</span>: number, <span class="hljs-attr">lat</span>: number, <span class="hljs-attr">height</span>: number},...,{<span class="hljs-attr">lng</span>: number, <span class="hljs-attr">lat</span>: number, <span class="hljs-attr">height</span>: number}],
