      <span class="hljs-attr">holes</span>: [
        <span class="hljs-attr">positions</span>: [{<span class="hljs-attr">lng</span>: number, <span class="hljs-attr">lat</span>: number, <span class="hljs-attr">height</span>: number},...,{<span class="hljs-attr">lng</span>: number, <span class="hljs-attr">lat</span>: number, <span class="hljs-attr">height</span>: number}]
        <span class="hljs-comment">// ...</span>
      ]
    }
  ]
}

</code></pre>
<p>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PolygonGeometry.html">PolygonGeometry</a></strong>、<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PolygonOutlineGeometry.html">PolygonOutlineGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometry: null,
          vertexFormat: null,
          polygonHierarchy: [
            { lng: 102.1, lat: 29.5 },
            { lng: 106.2, lat: 29.5 },
            { lng: 106.2, lat: 33.5 },
            { lng: 108.2, lat: 35.5 },
            { lng: 102.1, lat: 33.5 }
          ],
          height: 100000,
          polygonHierarchyOutline: [
            { lng: 107.1, lat: 29.5 },
            { lng: 111.2, lat: 29.5 },
            { lng: 111.2, lat: 33.5 },
            { lng: 113.2, lat: 35.5 },
            { lng: 107.1, lat: 33.5 }
          ],
          appearanceOutline: null,
          attributes: null
        }
      },
      mounted() {
        Promise.all([this.$refs.polygon.createPromise, this.$refs.polygonOutline.createPromise]).then((instances) => {
          console.log('All geometries are loaded.')
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = cur.vm.$parent.modelMatrix
              ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
              : geometry.boundingSphere
            return prev === null ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.viewer = viewer
          const { PerInstanceColorAppearance, ColorGeometryInstanceAttribute, EllipsoidSurfaceAppearance, Material } = Cesium
          this.appearanceOutline = new PerInstanceColorAppearance({
            flat: true
          })
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random(), 0.5)
          }
          this.vertexFormat = EllipsoidSurfaceAppearance.VERTEX_FORMAT
          this.appearance = new EllipsoidSurfaceAppearance({
            material: new Material({
              fabric: {
                type: 'Water',
                uniforms: {
                  normalMap: './statics/SampleData/images/waterNormals.jpg',
                  frequency: 1000.0,
                  animationSpeed: 0.05,
                  amplitude: 10.0
                }
              }
            })
          })
        }
      }
    }
  </script>